body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.card {
    box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2);
    max-width: 300px;
    margin: auto;
    text-align: center;
    font-family: arial;
    text-align: center;
  }
  
  .price {
    color: grey;
    font-size: 22px;
  }
  
  .card button {
    border: none;
    outline: 0;
    padding: 12px;
    color: white;
    background-color: #000;
    text-align: center;
    cursor: pointer;
    width: 100%;
    font-size: 18px;
  }
  
  .card button:hover {
    opacity: 0.7;
  }
  /* Float four columns side by side */
.column {
  float: left;
  width: 50%;
  padding: 0 10px;
}

/* Remove extra left and right margins, due to padding */
.row {margin: 0 -5px;}

/* Clear floats after the columns */
.row:after {
  content: "";
  display: table;
  clear: both;
}

/* Responsive columns */
@media screen and (max-width: 600px) {
  .column {
    width: 100%;
    display: block;
    margin-bottom: 20px;
  }
}

.containers {
    width:900px;
    margin:250px auto 0;
    display:flex;
}
.containers .box {
    position:relative;
    width: 300px;
    height:100px;
    box-sizing:border-box;
    text-align:center;
    margin:0 10px;
    background:#00171d;
    overflow:hidden;
    border-radius:4px;
    box-shadow:0 0 0 2px rgba(0,7,10,1);
}
.containers .box .icon {
    width:100%;
    height:100%;
    background:#00171d;
    -webkit-transition: 0.5s;
    transition: 0.5s;
}
.containers .box .icon .fa {
    font-size: 4em;
    line-height:100px;
    color: #0ff;
}
.containers .box:hover .icon {
    -webkit-transform:scale(0);
            transform:scale(0);
}
.containers .box .details {
    position:absolute;
    top:0;
    left:0;
    width:100%;
    height:100%;
    background:#03a9f4;
    -webkit-transition:0.5s;
    transition:0.5s;
    -webkit-transform:scale(2);
            transform:scale(2);
    opacity:0;
}
.containers .box:hover .details {
    -webkit-transform:scale(1);
            transform:scale(1);
    opacity:1;
}
.containers .box .details h3 {
    margin:0;
    padding:0;
    line-height:100px;
    font-size:24px;
    color:#fff;
}
.containers .box .details:nth-child(2) .details {
    background:#e91e63;
}
.containers .box .details:nth-child(2) .details {
    background:#607d8b;
}

